@font-face {
  font-family: 'TAEBAEKfont';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2310@1.0/TAEBAEKfont.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'LINESeedKR-Bd';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Bd.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'NanumBarunpen';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/NanumBarunpen.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

/* 수직 스크롤바 */
::-webkit-scrollbar {
  width: 12px;
}
::-webkit-scrollbar-track {
  background: #F9F7F7;
}

::-webkit-scrollbar-thumb {
  background: #1B1B2F;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
}

.btn-primary {
  font-family: 'TAEBAEKfont';
  --bs-btn-color: #121212;
  --bs-btn-bg: #EBCE64;
  --bs-btn-border-color: #EBCE64;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: #1B1B2F;
  --bs-btn-hover-border-color: #1B1B2F;
}


/* App */
.App {
  text-align: center;
  font-size: 12px;
  background-color: #121212;
}
.title{
  cursor: pointer;
  color: #EBCE64;
  -webkit-text-stroke: 1px #1B1B2F; 
  text-shadow: 2px 2px 2px #fff;
}

.menu-bar{
  font-family: 'TAEBAEKfont';
  background-color: #1B1B2F;
  padding: 50px 0;
  color: #fff;
  min-height: 695px;
}

.menu-bar p{
  font-size: 1.5rem;
  margin-bottom: 28px;
  cursor: pointer;
  text-shadow: 2px 2px 2px gray;
}
.menu-bar p:last-child{
  margin-bottom: 0;
}

.menu-photo{
  width: 200px;
  height: auto;
}

.menu-profile:hover, .menu-skills:hover,
.menu-projects:hover, .menu-archive:hover{
  transform: translateY(5px);
}

/* Main */
.intro{
  padding: 150px 100px;
  color: #e3e3e3;
}

.intro-1{
  font-size: 4rem;
  font-family: 'TAEBAEKfont';
}
.intro-2{
  font-size: 2rem;
  opacity: 0.7;
  font-family: 'TAEBAEKfont';
}

.intro p{
  margin-top: 70px;
  line-height: 40px;
  font-size: 1.3rem;
  font-family: 'LINESeedKR-Bd';
}

.intro-3 {
  opacity: 0;
  transition: opacity 2s;
}
.intro-3.show {
  opacity: 0.9;
}

/* About */
.about-fr{
  font-family: 'TAEBAEKfont';
  padding: 150px 200px;
  background-color: #fff;
  border-radius: 25px;
  height: 595px;
  margin-top: 50px;
  margin-bottom: 50px;
}

/* 프로필 뒤집기 */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.photo-fr {
  position: relative;
  width: 200px;
  height: 250px;
  border-radius: 20px;
  perspective: 500px;
  cursor: pointer;
}
.photo-fr .flip {
  width: 100%;
  height: 100%;
  transition: all .5s;
  transform-style: preserve-3d;
  border: 5px solid #1B1B2F;
  border-radius: 10%;
  background-color: transparent;
}

.about-fr:hover .flip {
  transform: rotateY(180deg);
}

.photo-fr .flip > img {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  backface-visibility: hidden;
}

.photo-fr .flip .photo2 {
  z-index: 10;
  padding-top: 10px;
  width: 130%;
  margin-left: -10px;
  margin-top: -10px;
}

.photo-fr .flip .photo {
  z-index: 5;
  transform: rotateY(180deg);
}

.photo{
  width: 100%;
  height: 100%;
  border-radius: 16px;
}



/*  */



.info p{
  font-size: 1.3rem;
  color: #1B1B2F;
}

.gray p{
  color: rgb(77, 77, 77);
}

/* Skills */
.skill-fr{
  padding: 24px 50px;
}

.font-box:hover, .back-box:hover,
.version-box:hover, .design-box:hover{
  transform: translateY(10px);
}

.skill-box{
  margin: 30px;
  padding: 20px 0;
  border-radius: 30px;
  background-color: #fff;
}
.skill-cate{
  font-family: 'TAEBAEKfont';
  color: #EBCE64;
  margin-top: -45px;
  margin-bottom: 50px;
  font-size: 2.3rem;
  -webkit-text-stroke: 1px #1B1B2F; 
  text-shadow: 1px #1B1B2F;
}

.front-logo3{
  width: 300px;
  height: auto;
  margin-top: -10px;
}
.front-logo{
  width: 100px;
  height: auto;
}

.back-logo{
  width: 200px;
  height: auto;
}

.java-logo{
  width: 140px;
  height: auto;
  margin-top: -10px;
  margin-bottom: 20px;
}

.version-logo{
  width: 120px;
  height: auto;
  margin-bottom: 10px;
}
.version-logo2{
  width: 80px;
  height: auto;
}
.design-logo{
  width: 120px;
  height: auto;
  margin-top: -20px;
}


/* Projects */
.project-fr{
  padding: 30px 50px;
  font-family: 'LINESeedKR-Bd';
}
.project-box{
  position: relative;
  text-align: left;
  margin: 30px;
  padding: 35px;
  border-radius: 30px;
  background-color: #fff;
  font-size: 1.1rem;
}
.project-box h5{
  font-family: 'TAEBAEKfont';
  margin-top: 10px;
}
.project-cate{
  font-family: 'TAEBAEKfont';
  position: absolute;
  top: 5px;
  left: -30px;
  width: 130px;
  padding: 2px;
  text-align: center;
  background-color: #EBCE64;
  border: 1px solid #000;
  transform: rotate(-30deg);
  opacity: 0.9;
}
.project-cate-back{
  font-family: 'TAEBAEKfont';
  position: absolute;
  top: 5px;
  right: -30px;
  width: 130px;
  padding: 2px;
  text-align: center;
  background-color: #F93121;
  border: 1px solid #000;
  transform: rotate(30deg);
  opacity: 0.9;
}
.project-cate-team{
  font-family: 'TAEBAEKfont';
  position: absolute;
  top: 5px;
  right: -30px;
  width: 150px;
  padding: 2px;
  text-align: center;
  background-color: #1B1B2F;
  border: 1px solid #fff;
  color: #fff;
  transform: rotate(30deg);
  opacity: 0.9;
}
.badge{
  margin-left: -10px;
}
.badge img{
  height: 25px;
}
.project-img{
  width: 320px;
  height: 300px;
  overflow: hidden;
  cursor: pointer;
}
.xexymix{
  width: 300px;
  height: auto;
}

.kakao{
  width: 300px;
  height: auto;
}

.portImg{
  width: 320px;
}

.greenLibrary-img{
  height: 200px;
}

.greenLibrary{
  margin-top: 20px;
  width: 300px;
  height: 200px;
  cursor: pointer;
}

.img-btn{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 320px;
  height: 50px;
}

.prev{
  text-indent: -9999px;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
  border-left: 4px solid #EBCE64;
  border-bottom: 4px solid #EBCE64;
  border-radius: 3px;
  cursor: pointer;
}
.next{
  text-indent: -9999px;
  width: 20px;
  height: 20px;
  transform: rotate(-45deg);
  border-right: 4px solid #EBCE64;
  border-bottom: 4px solid #EBCE64;
  border-radius: 3px;
  cursor: pointer;
}

.project-view{
  width: 70px;
  position: absolute;
  right: 40px;
  bottom: -20px;
}
.project-img:hover + .project-view{
  transform: translateY(-10px);
}
.modal-title{
  font-family: 'TAEBAEKfont';
  width: 100%;
  text-align: center;
  color: #EBCE64;
  text-shadow: 2px 2px 2px #1B1B2F;
}
.modal-body{
  height: 450px;
  text-align: center;
}

.greenMoa{
  width: 300px;
  height: auto;
  margin-top: 50px;
}

.library-show{
  width: 760px;
  height: auto;
}


.carousel-item{
  height: 400px;
}

.carousel-caption {
  font-family: 'LINESeedKR-Bd';
  right: 15%;
  bottom: 0;
  left: 15%;
  padding-bottom: 0;
  color: #000;
  text-align: center;
}

.carousel-control-next-icon, .carousel-control-prev-icon {
  background-image: none;
  width: 40px;
  height: 40px;
  border-right: 7px solid #EBCE64;
  border-bottom: 7px solid #EBCE64;
}
.carousel-control-next-icon{
  transform: rotate(-45deg);
}
.carousel-control-prev-icon{
  transform: rotate(135deg);
}

/* Archive */
.archive-fr{
  padding: 100px 70px;
  font-family: 'TAEBAEKfont';
  display: flex;
  align-items: end;
}

.myTistory{
  width: 500px;
}

.tistory-logo{
  width: 30px;
  margin-right: 2px;
}
.github-logo{
  width: 23px;
  margin-right: 5px;
}
.blog-logo{
  width: 23px;
  margin-right: 6px;

}
.my-blog{
  width: 150px;
}

.card-content{
  font-family: 'LINESeedKR-Bd';
  font-size: 1.2rem;
}

.tistory-card:hover, .github-card:hover, .blog-card:hover{
  transform: translateY(10px);
}



    